// Icons

// Fonts
@import url('https://fonts.googleapis.com/css?family=Raleway:400,500,600,700,800');

@font-face {
	font-family: 'Roboto';
	src: url(../fonts/Roboto/Roboto-Regular.ttf);
	font-weight: 400; }
@font-face {
	font-family: 'Montserrat';
	src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
	font-weight: 400; }
@font-face {
	font-family: 'Montserrat';
	src: url(../fonts/Montserrat/Montserrat-ExtraLight.ttf);
	font-weight: 200; }
@font-face {
	font-family: 'Montserrat';
	src: url(../fonts/Montserrat/Montserrat-Light.ttf);
	font-weight: 300; }

@mixin fnt-raleway($weight, $size, $line-height) {
	font-family: 'Raleway';
	font-weight: $weight;
	font-size: $size;
	line-height: $line-height; }
@mixin fnt-roboto($weight, $size, $line-height) {
	font-family: 'Roboto';
	font-weight: $weight;
	font-size: $size;
	line-height: $line-height; }
@mixin fnt-montseratt($weight, $size, $line-height) {
	font-family: 'Montserrat';
	font-weight: $weight;
	font-size: $size;
	line-height: $line-height; }
