	&-slider {
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: center;
		&__item {
			position: relative;
			width: 22px;
			height: 22px;
			border: 1px solid #d1d1d1;
			border-radius: 50%;
			margin-left: 8px;
			&::after {
				position: absolute;
				display: block;
				content: '';
				width: 6px;
				height: 6px;
				border-left: 2px solid #d1d1d1;
				border-bottom: 2px solid #d1d1d1;
				top: 28%;
				left: 36%; }
			&--left::after {
				transform: rotate(45deg); }
			&--right::after {
				transform: rotate(-135deg);
				left: 26%; } } } }
